@import url(https://fonts.googleapis.com/css?family=Trade+Winds&display=swap);
@font-face {
  font-family: "Digital dream";
  src: url(/react-drum/static/media/DIGITALDREAM.ee9072cf.ttf);
}

.drum-controls-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
}

.drum-pad {
  /* width: 70px;
  height: 70px; */
  width: 100%;
  height: 75%;
  border-radius: 1px;
  background: black;
  border: 2px solid red;
  color: red;
  border-radius: 10px;
  font-family: "Trade Winds", cursive;
  font-size: 2.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.drum-pad:hover {
  cursor: pointer;
  background: red;
  color: black;
}

.volume-control-container {
  width: 40%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.volume-control {
  width: 100%;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  height: 15px;
  background: grey;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
  border-radius: 20px;
  position: relative;
  top: 100px;
}

.volume-control::-webkit-slider-thumb {
  -webkit-appearance: none;
          appearance: none;
  width: 25px;
  height: 25px;
  background: red;
  border-radius: 50%;
}

@media screen and (orientation: landscape) {
  .drum-pad {
    width: 85%;
    height: 85%;
  }

  .volume-control {
    width: 70%;
    top: 80px;
  }
}

