html,
body {
  font-size: 10px;
}

html {
  height: 100%;
}

body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  height: 100%;
}

#root {
  display: flex;
  justify-content: center;
  height: 100%;
  align-items: center;
}

